import { EventEmitter, Output } from '@angular/core';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-tryon-list',
  templateUrl: './tryon-list.component.html',
  styleUrls: ['./tryon-list.component.css']
})
export class TryonListComponent implements OnInit {

  @Output() jewelrySelectedEvent = new EventEmitter<string>();

  jewelrys: Food[] = [
    {value: 'jewelry1.png', viewValue: 'jewelry1'},
    {value: 'jewelry2.png', viewValue: 'jewelry2'},
    {value: 'jewelry3.png', viewValue: 'jewelry3'}
  ];
  private selectedjewelry = 'jewelry1.png';


  constructor() { }

  ngOnInit() {
  }

  jewelrySelected($event) {
    this.jewelrySelectedEvent.emit($event.value);
  }

}

interface Food {
  value: string;
  viewValue: string;
}



