import { Component, OnInit, OnDestroy, Input } from "@angular/core";
import * as p5 from "p5";
import * as ml5 from "ml5";
import "p5/lib/addons/p5.dom";

declare let ml5: any;

@Component({
  selector: 'app-tryon-video',
  templateUrl: './tryon-video.component.html',
  styleUrls: ['./tryon-video.component.css']
})
export class TryonVideoComponent implements OnInit {

  private p5;

  @Input() selectedjewelry: string;

  constructor() {
    console.log("WingoldNext-tryon-constructed");
  }

  ngOnInit() {
    console.log("WingoldNext-tryon-init");
    //this.createCanvas()
  }
  ngOnDestroy(): void {
    this.createCanvas();
    console.log("WingoldNext-tryon-destroy");
  }

  ngOnChanges() {
    console.log("WingoldNext-tryon-Change");
    this.p5 =this.createCanvas()
    }

  private createCanvas = () => {

      console.log("creating canvas" + this.selectedjewelry);
      const mySketch = this.defiendDrawing(this.selectedjewelry);
      this.p5 = new p5(mySketch);

  };


  private defiendDrawing(strpath) {
    return function (p: any) {
      let video;
      let poseNet;
      let poses = [];
      let jewelry
      var oldkeypoint = new Array(17);

      console.log('drawing');
      console.log(strpath);

      p.setup = () => {
        var myDiv = document.getElementById("try-on-canvas");
        myDiv.innerHTML = "";//remove
        p.createCanvas(640, 480).parent("try-on-canvas");
        let video = p.createCapture(p.VIDEO);
        video.size(640, 480);

        poseNet = ml5.poseNet(video, modelReady);
        poseNet.on("pose", function (results) {
          poses = results;
          //console.log(poses);
        });
        video.hide();
        jewelry = p.createImg('http://webmasterstechnologies.com/WingoldNext/App/images/EarRing/' + strpath)
        jewelry.hide();


        p.draw = () => {
          p.image(video, 0, 0, 640, 480);
          drawKeypoints();
        };
      };

      function drawKeypoints() {
        // Loop through all the poses detected

        for (let i = 0; i < poses.length; i++) {
          // For each pose detected, loop through all the keypoints
          let pose = poses[i].pose;
          const dist = getDistance(pose)
          for (let j = 0; j < pose.keypoints.length; j++) {

            switch (pose.keypoints[j].part) {
              case "leftEar":
                drawLeftRightEar(pose.keypoints[j], j);
                break;
              case "rightEar":
                drawLeftRightEar(pose.keypoints[j], j);
                break;
              default:
            }
          }
          //}
        }
      }

      function getDistance(pose) {


        const lx = pose.keypoints[0].position.x
        const ly = pose.keypoints[0].position.y
        const rx = pose.keypoints[1].position.x
        const ry = pose.keypoints[1].position.y
        const retval = p.dist(lx, ly, rx, ry)
        return retval

      }



      function drawLeftRightEar(keypoint, j) {

        if (keypoint.score > 0.55) {
          if (!oldkeypoint[j]) {
            oldkeypoint[j] = keypoint;
          }
          else {
            if ((Math.abs(Math.trunc(oldkeypoint[j].position.x) - Math.trunc(keypoint.position.x)) > 3) ||
              (Math.abs(Math.trunc(oldkeypoint[j].position.y) - Math.trunc(keypoint.position.y)) > 10)) {
              oldkeypoint[j] = keypoint;
            }
          }


          p.fill(255, 0, 0);
          p.noStroke();
          p.push();
          p.imageMode(p.CENTER);
          p.image(jewelry,
            Math.trunc(keypoint.position.x),
            Math.trunc(keypoint.position.y) + 60, 30, 60)
          p.pop();

        }
      }

      function modelReady() {
        console.log("Model ready");
      }
    }
  }



}



